@import url('https://fonts.googleapis.com/css?family=Crete+Round&display=swap');
.chart {
  display: flex;
  width: 100%;
  height: 100vh;
  text-align: center;
  justify-content: center;
  align-content: center;
}

canvas { display: block; }


.sankey{
  height: 400px;
  width: 700px;
}

.sankeysection{
  height: 100vh;
  width: 100%;
  position: relative;
}

#background-video{

  height: 100vh;
  width: 100%;
  float: left;
  top: 0;
  padding: none;
  position: fixed; /* optional depending on what you want to do in your app */
  
  }




.primeravirus {
  display: flex;
  width: 100%;
  height: relative;
  text-align: center;
  justify-content: center;
  align-content: center;
}

.primeravirus svg {
  width: 100%;
  margin-top: 2%;
}

section { width: 100%;
          margin: 0 auto;
          height: 100vh;
      }


.frontvideo { width: 100%;
          height: 100vh;
      }

.content {
  display: grid;
  grid-template-columns: 70% auto;

  .left {
    position: relative;
    text-align: center;
    display: grid;
    place-items: center;

    p {
      background: white;
      position: absolute;
      margin-top: -25%;
      right: 17%;
      padding: 1em;
      border-radius: 15px;
      width: 20vw;
      text-align: left;
    }

  }

  .img1 {
    width: 50%;
  }

    .right {
      display: flex;
      place-items: center;

      h2{
        color: green;
        font-size: 1.7rem;  
      }

      p {
        line-height: 1.6em;
      }
    }

}

@keyframes anim1 {
  from {
    transform: translateY(-100px);
    opacity: 0;
  }
  
  to {
    transform: translateY(0);
    opacity: 1;
  }
}



.fullscreen-video-wrap{
  position:absolute;
  top:0;
  left:0;
  width:100%;
  height:100vh;
  overflow:hidden;
}

.fullscreen-video-wrap video{
  min-height:100%;
  min-width:100%;
}

.header-overlay{
  height:100vh;
  position: absolute;
  top:0;
  left:0;
  width:100vw;
  z-index:1;
  background:#225470;
  opacity:0.85;
}

.header-content{
  z-index:2;
}

.header-content h1{
  font-size:50px;
  margin-bottom:0;
}

.header-content p{
  font-size:1.5rem;
  display:block;
  padding-bottom:2rem;
}


.v-header{
  height:100vh;
  display:flex;
  align-items:center;
  color:#ffffff;
}

.video-container {
  position: relative;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%; 
  overflow: hidden;
  z-index:0;
}
.video-container video {
  /* Make video to at least 100% wide and tall */
  min-width: 100%; 
  min-height: 100%; 

  /* Setting width & height to auto prevents the browser from stretching or squishing the video */
  width: auto;
  height: auto;

  /* Center the video */
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
}

.overlay {
  position: absolute;
  top:0;
  left:0;
  z-index:1;
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}

.overlay h1 {
  font-family: 'Crete Round', serif;
  text-transform: uppercase;
  position: relative;
  color: red;
  overflow: hidden;
  height: 150px;
  margin: 0;
}


#upperWrap, #lowerWrap {
  overflow: hidden;
  height: 80px;
  line-height: 70px;
  width: 100%;
  font-weight: 700;
}

#upper {
  font-size: 70px;
  text-transform: uppercase
}

#line {
  width: 100%;
  height: 1px;
  background: white;
}

#lower {
  font-size: 30px;
  font-family: 'Crete Round', serif;

}

.Uncontrolled {
  color: white;
}