ection:column;justify-content:center;text-align:center}.overlay h1{font-family:'Crete Round', serif;text-transform:uppercase;position:relative;color:red;overflow:hidden;height:150px;margin:0}#upperWrap,#lowerWrap{overflow:hidden;height:80px;line-height:70px;width:100%;font-weight:700}#upper{font-size:70px;text-transform:uppercase}#line{width:100%;height:1px;background:white}#lower{font-size:30px;font-family:'Crete Round', serif}.Uncontrolled{color:white}

